import React, {useState, useEffect, useCallback, useRef} from 'react'
import { ReactSortable } from 'react-sortablejs'
import { useSelector, useDispatch } from 'react-redux'
import { Button, Avatar, Dropdown, Menu, Tabs } from 'antd'
import debounce from 'lodash.debounce'
import { timeFormat } from 'd3-time-format'
import {
  PlusOutlined,
  PaperClipOutlined,
} from '@ant-design/icons'
import HelpdeskMenu from 'components/cleanui/system/Helpdesk/Menu'
import usePrevious from 'hooks/usePrevious'
import style from './style.module.scss'

const { TabPane } = Tabs

const dropdownMenu = (
  <Menu>
    <Menu.Item>
      <a>Action</a>
    </Menu.Item>
    <Menu.Item>
      <a>Another action</a>
    </Menu.Item>
    <Menu.Item>
      <a>Something else here</a>
    </Menu.Item>
    <div className="dropdown-divider" />
    <Menu.Item>
      <a>Separated link</a>
    </Menu.Item>
  </Menu>
)

function shuffle(array) {
  let currentIndex = array.length;
  let temporaryValue;
  let randomIndex;

  while (currentIndex !== 0) {
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;

    temporaryValue = array[currentIndex];
    array[currentIndex] = array[randomIndex];
    array[randomIndex] = temporaryValue;
  }

  return array;
}

const KanbanItem = ({ item }) => {
  const { key, name, ticket_number: ticketNumber, receive_time: receiveTime, response_time: responseTime } = item
  const lineColors = shuffle(['bg-danger', 'bg-primary', 'bg-warning', 'bg-success'])
  const backgrounds = shuffle(['#9B51E0', '#F1C40F', '#219653'])
  const daysLeft = shuffle([true, false])
  return (
    <div className={`${style.card} ${style.cardItem}`} key={key}>
      <div className={style.content}>
        <div className="d-flex flex-wrap-reverse align-items-center justify-content-between">
          <div>
            <span className={`${style.flag} d-inline-block ${lineColors[0]}`}>&nbsp;</span>
            <span className={`${style.flag} d-inline-block ${lineColors[1]}`}>&nbsp;</span>
          </div>
          { daysLeft[0] ? <span className="text-danger">2 days left</span> : null }
        </div>
        <div className="d-flex flex-wrap-reverse align-items-center">
          <h5 className="text-dark font-size-18 mt-2 mr-auto">{name}/{ticketNumber}</h5>
        </div>
        <div className="text-gray-5 mb-2">Deadline: {receiveTime && timeFormat('%Y-%m-%d %H:%M:%S')(new Date(receiveTime))}/{responseTime && timeFormat('%Y-%m-%d %H:%M:%S')(new Date(responseTime))}</div>
        <div className="d-flex align-items-center flex-wrap pb-2 justify-content-between">
          <div className="d-flex">
            <div className="kit__utils__avatar kit__utils__avatar--rounded kit__utils__avatar--size27 flex-shrink-0 mr-2">
              <Avatar size={28} src="resources/images/avatars/1.jpg" />
            </div>
            <div className="kit__utils__avatar kit__utils__avatar--rounded kit__utils__avatar--size27 flex-shrink-0 mr-2">
              <Avatar size={28} src="resources/images/avatars/2.jpg" />
            </div>
            <div className="kit__utils__avatar kit__utils__avatar--rounded kit__utils__avatar--size27 flex-shrink-0 mr-2">
              <Avatar size={28} src="resources/images/avatars/3.jpg" />
            </div>
            <div className="kit__utils__avatar kit__utils__avatar--rounded kit__utils__avatar--size27 flex-shrink-0 mr-2">
              <Avatar size={28} style={{color: '#ffffff', backgroundColor: backgrounds[0]}}>SR</Avatar>
            </div>
          </div>
          { daysLeft[0] ? <Button type="link" icon={<PaperClipOutlined />} /> : null }
        </div>
      </div>
    </div>
  )
}

const HelpdeskKanban = () => {
  const { list: sourceList, total, loading, limit } = useSelector(state => state.helpdesk)
  const infiniteLoader = useRef(null)
  const dispatch = useDispatch()
  const previousList = usePrevious(sourceList) || []
  const [page, setPage] = useState(1)
  const [totalPage, setTotalPage] = useState(1)
  const [list, setList] = useState([])
  const filterByStatus = useCallback(statusName => {
    const filteredList = [...list].filter(data => data.status_name === statusName)
    return filteredList.length > 0
      ? filteredList.map(data => (
        <KanbanItem item={data} key={data.guid} />
      )) : []
  }, [list])
  const [backlogItems, setBacklogItems] = useState([])
  const [todoItems, setTodoItems] = useState([])
  const [inprogressItems, setInprogressItems] = useState([])
  const [completed, setCompleted] = useState([])
  const isArrayOfObjectEqual = useCallback(
    (array1, array2) => (array1.length === array2.length) &&
      array1.every((element, index) => {
        return JSON.stringify(element) === JSON.stringify(array2[index]);
      }), []
  )

  const handleObserver = useCallback(debounce((entities) => {
    const target = entities[0];
    if (target.isIntersecting && page < totalPage && !loading) {
      setPage(page + 1)
    }
  }, 600), [page, totalPage, loading])

  useEffect(() => {
    setBacklogItems(filterByStatus('Backlog'))
    setTodoItems(filterByStatus('Open'))
    setInprogressItems(filterByStatus('In Progress'))
    setCompleted(filterByStatus('Done'))
  }, [list, filterByStatus])

  useEffect(() => {
    if (!isArrayOfObjectEqual(sourceList, previousList)) {
      setList(l =>[
        ...l,
        ...sourceList,
      ])
    }
  }, [sourceList, previousList, isArrayOfObjectEqual])

  useEffect(() => {
    if (page > 1) {
      dispatch({
        type: 'helpdesk/SEARCH_HELPDESK',
        payload: {
          start: Number(page),
        }
      })
    }
  }, [page, dispatch])

  useEffect(() => {
    const options = {
      root: null,
      rootMargin: '20px',
      threshold: 1.0
    };

    const observer = new IntersectionObserver(handleObserver, options);
    if (infiniteLoader.current) {
      observer.observe(infiniteLoader.current)
    }
  }, [handleObserver])

  useEffect(() => {
    setTotalPage(Math.round(total / limit))
  }, [total, limit])

  return (
    <div className={loading ? style.loading : ''}>
      <div className="d-flex justify-content-between mb-4">
        <Tabs className={style['contain-tabs']}>
          <TabPane key="1" tab={<Button type="primary" size="large" icon={<PlusOutlined />}>Create New</Button>} />
        </Tabs>
        <div className={style['contain-views']}>
          <HelpdeskMenu />
        </div>
      </div>
      <div className="row">
        <div className="col-lg-3 col-md-6 d-flex">
          <div className="card bg-light py-3 px-3 w-100">
            <div className={`card-header card-header-flex border-bottom-0 p-0 align-items-start ${style['card-header']}`}>
              <div className={`d-flex flex-column justify-content-center align-items-start ${style['card-title']}`}>
                <h3 className="font-weight-bold text-dark font-size-18 mb-0">Backlog ({backlogItems.length})</h3>
              </div>
              <div className={`ml-auto d-flex flex-column justify-content-center ${style['card-menu']}`}>
                <div className="dropdown d-inline-block">
                  <Dropdown overlay={dropdownMenu} placement="bottomRight">
                    <button
                      type="button"
                      className="btn btn-light dropdown-toggle dropdown-toggle-noarrow"
                    >
                      <i className="fe fe-more-horizontal" />
                    </button>
                  </Dropdown>
                </div>
              </div>
            </div>
            <div className={`d-flex flex-column justify-content-between ${style['card-inner']}`}>
              <div>
                <ReactSortable
                  group="issues"
                  style={{minHeight: 500}}
                  list={backlogItems}
                  setList={setBacklogItems}
                  animation={300}
                >
                  {backlogItems.map(item => item)}
                </ReactSortable>
              </div>
              <Button size="large" type="primary">
                <i className="fe fe-plus d-inline-block mr-2" />
                Add new card
              </Button>
            </div>
          </div>
        </div>
        <div className="col-lg-3 col-md-6 d-flex">
          <div className="card bg-light py-3 px-3 w-100">
            <div className={`card-header card-header-flex border-bottom-0 p-0 align-items-start ${style['card-header']}`}>
              <div className={`d-flex flex-column justify-content-center align-items-start ${style['card-title']}`}>
                <h3 className="font-weight-bold text-dark font-size-18 mb-0">To Do ({todoItems.length})</h3>
              </div>
              <div className={`ml-auto d-flex flex-column justify-content-center ${style['card-menu']}`}>
                <div className="dropdown d-inline-block">
                  <Dropdown overlay={dropdownMenu} placement="bottomRight">
                    <button
                      type="button"
                      className="btn btn-light dropdown-toggle dropdown-toggle-noarrow"
                    >
                      <i className="fe fe-more-horizontal" />
                    </button>
                  </Dropdown>
                </div>
              </div>
            </div>
            <div className={`d-flex flex-column justify-content-between ${style['card-inner']}`}>
              <div>
                <ReactSortable
                  group="issues"
                  style={{minHeight: 500}}
                  list={todoItems}
                  setList={setTodoItems}
                  animation={300}
                >
                  {todoItems.map(item => item)}
                </ReactSortable>
              </div>
              <Button size="large" type="primary">
                <i className="fe fe-plus d-inline-block mr-2" />
                Add new card
              </Button>
            </div>
          </div>
        </div>
        <div className="col-lg-3 col-md-6 d-flex">
          <div className="card bg-light py-3 px-3 w-100">
            <div className={`card-header card-header-flex border-bottom-0 p-0 align-items-start ${style['card-header']}`}>
              <div className={`d-flex flex-column justify-content-center align-items-start ${style['card-title']}`}>
                <h3 className="font-weight-bold text-dark font-size-18 mb-0">In Progress ({inprogressItems.length})</h3>
              </div>
              <div className={`ml-auto d-flex flex-column justify-content-center ${style['card-menu']}`}>
                <div className="dropdown d-inline-block">
                  <Dropdown overlay={dropdownMenu} placement="bottomRight">
                    <button
                      type="button"
                      className="btn btn-light dropdown-toggle dropdown-toggle-noarrow"
                    >
                      <i className="fe fe-more-horizontal" />
                    </button>
                  </Dropdown>
                </div>
              </div>
            </div>
            <div className={`d-flex flex-column justify-content-between ${style['card-inner']}`}>
              <div>
                <ReactSortable
                  group="issues"
                  style={{minHeight: 500}}
                  list={inprogressItems}
                  setList={setInprogressItems}
                  animation={300}
                >
                  {inprogressItems.map(item => item)}
                </ReactSortable>
              </div>
              <Button size="large" type="primary">
                <i className="fe fe-plus d-inline-block mr-2" />
                Add new card
              </Button>
            </div>
          </div>
        </div>
        <div className="col-lg-3 col-md-6 d-flex">
          <div className="card bg-light py-3 px-3 w-100">
            <div className={`card-header card-header-flex border-bottom-0 p-0 align-items-start ${style['card-header']}`}>
              <div className={`d-flex flex-column justify-content-center align-items-start ${style['card-title']}`}>
                <h3 className="font-weight-bold text-dark font-size-18 mb-0">Completed ({completed.length})</h3>
              </div>
              <div className={`ml-auto d-flex flex-column justify-content-center ${style['card-menu']}`}>
                <div className="dropdown d-inline-block">
                  <Dropdown overlay={dropdownMenu} placement="bottomRight">
                    <button
                      type="button"
                      className="btn btn-light dropdown-toggle dropdown-toggle-noarrow"
                    >
                      <i className="fe fe-more-horizontal" />
                    </button>
                  </Dropdown>
                </div>
              </div>
            </div>
            <div className={`d-flex flex-column justify-content-between ${style['card-inner']}`}>
              <div>
                <ReactSortable
                  group="issues"
                  style={{minHeight: 500}}
                  list={completed}
                  setList={setCompleted}
                  animation={300}
                >
                  {completed.map(item => item)}
                </ReactSortable>
              </div>
              <Button size="large" type="primary">
                <i className="fe fe-plus d-inline-block mr-2" />
                Add new card
              </Button>
            </div>
          </div>
        </div>
      </div>
      {
        page < totalPage
          ? (
            <div className="w-100 d-flex justify-content-center py-3">
              <Button type="primary" className="width-200" loading>
                <span ref={infiniteLoader}>Loading more...</span>
              </Button>
            </div>
          ) : null
      }
    </div>
  )
}

export default HelpdeskKanban
