import React from 'react'
import { Link } from 'react-router-dom'
import { useLocation } from 'react-router'
import { Button, Select } from 'antd'
import { useDispatch } from 'react-redux'
import NProgress from 'nprogress'
import { ReloadOutlined } from '@ant-design/icons'
import style from './style.module.scss'

const HelpdeskMenu = () => {
  const dispatch = useDispatch()
  const useQuery = () => {
    return new URLSearchParams(useLocation().search)
  }
  const query = useQuery()
  const view = query.get('view') || 'list'
  const reload = async () => {
    NProgress.start()
    await dispatch({
      type: 'helpdesk/LOAD_HELPDESK'
    })
    NProgress.done()
  }
  return (
    <div>
      {
        view === 'grid'
          ? (
            <Select defaultValue="Status" className={`${style.statusSelect} mr-2`}>
              <Select.Option value="offering">Offering</Select.Option>
              <Select.Option value="offering_approved">Approved</Select.Option>
              <Select.Option value="project_in_progress">In Progress</Select.Option>
              <Select.Option value="bast_done">Done</Select.Option>
            </Select>
          )
          : null
      }
      <Link to="">
        <Button
          className={`${style['btn-menu']} ${view === 'list' ? style.active : ''}`}
          type="link"
          size="large"
          icon={<i className="icmn-list" />}
        />
      </Link>
      <Link to="?view=grid">
        <Button
          className={`${style['btn-menu']} ${view === 'grid' ? style.active : ''}`}
          type="link"
          size="large"
          icon={<i className="fa fa-th-large" />}
        />
      </Link>
      <Link to="?view=kanban">
        <Button
          className={`${style['btn-menu']} ${view === 'kanban' ? style.active : ''}`}
          type="link"
          size="large"
          icon={<i className="icmn-trello" />}
        />
      </Link>
      <Button
        onClick={reload}
        className="ml-3"
        type="primary"
        size="large"
        icon={<ReloadOutlined />}
      />
    </div>
  )
}

export default HelpdeskMenu
