import React, { useState, useEffect, useCallback, useRef } from 'react'
import { ReactSortable } from 'react-sortablejs'
import { useSelector, useDispatch } from 'react-redux'
import { Button, Tabs } from 'antd'
import debounce from 'lodash.debounce'
import {
  DownloadOutlined,
  PlusOutlined,
  DeleteFilled,
} from '@ant-design/icons'
import HelpdeskMenu from 'components/cleanui/system/Helpdesk/Menu'
import usePrevious from 'hooks/usePrevious'
import style from './style.module.scss'

const { TabPane } = Tabs
const badgeColors = {
  'Open': 'bg-primary',
  'In Progress': 'bg-warning',
  'Done': 'bg-success',
  'Backlog': 'bg-danger'
}
const CardItem = ({ item }) => {
  const { key, name, ticket_number: ticketNumber, status_name: statusName } = item
  return (
    <div className={`card ${style.cardItem}`} key={key}>
      <div className={style.content}>
        <div className={`${style.flag} ${badgeColors[statusName]}`} />
        <div className="d-flex flex-wrap-reverse align-items-end justify-content-between">
          <div className={style.cardTitle}>
            <h5 className="text-dark font-size-18 mt-2 mr-auto">{name}/{ticketNumber}</h5>
            <div className="text-gray-5 mb-2">0 of 0 items</div>
          </div>
          <div className="mt-2">
            <Button
              shape="circle"
              icon={<DeleteFilled />}
              size="large"
              className={style['btn-delete']}
            />
          </div>
        </div>
        <div className="d-flex align-items-center flex-wrap pb-2">
          <div className="text-gray-5 text-center w-100 py-2 mt-4">
            <span>Data not found</span>
          </div>
        </div>
      </div>
    </div>
  )
}

const ListContainer = ({ list, setList }) => {
  return (
    <div className={style.listWithEmpty}>
      {
        list.length < 1
          ? (
            <div className={`w-100 align-items-center d-flex flex-column card p-4 ${style.emptyContainer}`}>
              <i className={`${style.emptyIcon} fe fe-inbox`} />
              <p className="mt-2">This is currently empty</p>
            </div>
          ) : null
      }
      <ReactSortable
        group="issues"
        style={{ minHeight: 500 }}
        list={list}
        setList={setList}
        animation={300}
      >
        {list.map(item => item)}
      </ReactSortable>
    </div>
  )
}

const HelpdeskGrid = () => {
  const { list: sourceList, total, loading, limit } = useSelector(state => state.helpdesk)
  const infiniteLoader = useRef(null)
  const dispatch = useDispatch()
  const previousList = usePrevious(sourceList) || []
  const [page, setPage] = useState(1)
  const [totalPage, setTotalPage] = useState(1)
  const [list, setList] = useState([])
  const [offeringItems, setOfferingItems] = useState([])
  const [offeringApprovedItems, setOfferingApprovedItems] = useState([])
  const [projectOnProgressItems, setProjectOnProgressItems] = useState([])
  const [bastDoneItems, setBastDoneItems] = useState([])

  const filterByStatus = useCallback(statusName => {
    const filteredList = [...list].filter(data => data.status_name === statusName)
    return filteredList.length > 0
      ? filteredList.map(data => (
        <CardItem item={data} key={data.guid} />
      )) : []
  }, [list])

  const isArrayOfObjectEqual = useCallback(
    (array1, array2) => (array1.length === array2.length) &&
      array1.every((element, index) => {
        return JSON.stringify(element) === JSON.stringify(array2[index]);
      }), []
  )

  const handleObserver = useCallback(debounce((entities) => {
    const target = entities[0];
    if (target.isIntersecting && page < totalPage && !loading) {
      setPage(page + 1)
    }
  }, 600), [page, totalPage, loading])

  useEffect(() => {
    setOfferingItems(filterByStatus('Backlog'))
    setOfferingApprovedItems(filterByStatus('Open'))
    setProjectOnProgressItems(filterByStatus('In Progress'))
    setBastDoneItems(filterByStatus('Done'))
  }, [list, filterByStatus])

  useEffect(() => {
    if (!isArrayOfObjectEqual(sourceList, previousList)) {
      setList(l =>[
        ...l,
        ...sourceList,
      ])
    }
  }, [sourceList, previousList, isArrayOfObjectEqual])

  useEffect(() => {
    if (page > 1) {
      dispatch({
        type: 'helpdesk/SEARCH_HELPDESK',
        payload: {
          start: Number(page),
        }
      })
    }
  }, [page, dispatch])

  useEffect(() => {
    const options = {
      root: null,
      rootMargin: '20px',
      threshold: 1.0
    };

    const observer = new IntersectionObserver(handleObserver, options);
    if (infiniteLoader.current) {
      observer.observe(infiniteLoader.current)
    }
  }, [handleObserver])

  useEffect(() => {
    setTotalPage(Math.round(total / limit))
  }, [total, limit])

  return (
    <div className={loading ? style.loading : ''}>
      <div className="d-flex justify-content-between mb-4">
        <Tabs className={style['contain-tabs']}>
          <TabPane key="1" tab={<Button type="primary" size="large" icon={<PlusOutlined />}>Create New</Button>} />
          <TabPane key="2" tab={<Button size="large" icon={<DownloadOutlined />} className="ml-3">Download</Button>} />
        </Tabs>
        <div className={style['contain-views']}>
          <HelpdeskMenu />
        </div>
      </div>
      <div className="row">
        <div className="col-lg-3 col-md-6">
          <ListContainer list={offeringItems} setList={setOfferingItems} />
        </div>
        <div className="col-lg-3 col-md-6">
          <ListContainer list={offeringApprovedItems} setList={setOfferingApprovedItems} />
        </div>
        <div className="col-lg-3 col-md-6">
          <ListContainer list={projectOnProgressItems} setList={setProjectOnProgressItems} />
        </div>
        <div className="col-lg-3 col-md-6">
          <ListContainer list={bastDoneItems} setList={setBastDoneItems} />
        </div>
      </div>
      {
        page < totalPage
          ? (
            <div className="w-100 d-flex justify-content-center py-3">
              <Button type="primary" className="width-200" loading>
                <span ref={infiniteLoader}>Loading more...</span>
              </Button>
            </div>
          ) : null
      }
    </div>
  )
}

export default HelpdeskGrid
