import React from 'react'
import { useLocation } from 'react-router'
import { connect } from 'react-redux'
import HelpdeskList from './list'
import HelpdeskKanban from './kanban'
import HelpdeskGrid from './grid'

const mapStateToProps = ({ helpdesk, dispatch }) => ({
  dispatch,
  list: helpdesk.list,
  loading: helpdesk.loading
})

const useQuery = () => {
  return new URLSearchParams(useLocation().search)
}

const Helpdesk = ({ list }) => {
  const query = useQuery()
  const view = query.get('view') || 'list'

  return (
    <div>
      <div className="row">
        <div className="col-lg-12">
          {
            view === 'list' && (
              <HelpdeskList list={list} />
            )
          }
          {
            view === 'grid' && (
              <HelpdeskGrid />
            )
          }
          {
            view === 'kanban' && (
              <HelpdeskKanban />
            )
          }
        </div>
      </div>
    </div>
  )
}

export default connect(mapStateToProps)(Helpdesk)
